html,
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100%;
  overflow-x: hidden;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root{
  --bg: #242526;
  --bg-accent: #484a4d;
  --text-color: #dadce1;
  --nav-size: 60px;
  --border: 1px solid #474a4d;
  --border-radius: 8px;
  --speed: 500ms;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

a {
  color: #dadce1;
  color: var(--text-color);
  text-decoration: none;
}

p {
  margin-bottom: 0;
  text-align: center;
}















.grid-container{
    position: absolute;
    top: 60px; /* Adjust the top position to accommodate the navbar height */
    left: 0; /* Align the grid container to the left edge */
    /* height: calc(100vh - 60px);  */
    /* right: 0;
    bottom: 0; */
    width: 100%; /* Make the grid container span the full width */
    
    /* background-color: #aaaaaa; */
    display: flex;
    justify-content: center;
    align-items: center;
    
    z-index: 1;
    overflow-y: scroll;
    /* overflow-x: hidden;       */
    padding-top: 30px;  
    padding-bottom: 30px;         
    height: -webkit-max-content;         
    height: max-content;
    
}
  
.album-cover-grid{
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    grid-template-rows: repeat(4, 1fr); 
    grid-gap: 30px; 
    gap: 30px;
    max-width: 900px;
    grid-template-columns: repeat(auto-fit, minmax(152px, 1fr));
    /* overflow-y: scroll; */
    overflow-x: hidden;
    
}

.album-hover{
    width: 136px;
    height: 75px;
    color: white;
    font-size: 12px;
    align-items: center;
    justify-content: center;
    z-index: 2;
    position: absolute;
    visibility: hidden;
    -webkit-transform: translate(6%, -205%);
            transform: translate(6%, -205%);
    /* overflow: hidden; */
    opacity: 0;
    transition: opacity 0.3s ease;
}

.album-hover.visible {
    opacity: 1;
    visibility: visible;
}

.album-cover{
    /* width: 100%;
    height: 100%; */
    width: 150px;
    height: 150px;
    border-radius: 5%;
    object-fit: cover;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    z-index: 1;
    border: .75px solid black;
}

.album-cover.hovered{
    -webkit-filter: brightness(0.5);
            filter: brightness(0.5);
}

.album-container{
    justify-content: center;
    align-items: center;
    overflow: hidden;
    width: 152px;
    height: 152px;
    
  }
  
  .add-button{
    width: 36px;
    height: 36px;
    border-radius: 50%;
    overflow: hidden;
    position: relative;
  }
  .add-button:hover{
    -webkit-filter: brightness(1.2);
            filter: brightness(1.2);
  }
  
  .add-container{
    position: relative;
    width: 36px;
    height: 36px;
    justify-content: center;
    align-items: center;
    left: 0%;
    z-index: 3;
    -webkit-transform: translate(20%,-100%);
            transform: translate(20%,-100%);
  }
  

@media (max-width: 900px) {
    .album-cover-grid {
      grid-template-columns: repeat(4, 1fr);
      grid-template-rows: repeat(5, 1fr);
    }
  }

@media (max-width: 800px){
    .grid-container{
        top: 120px;
        /* height: calc(100vh - 180px);  */
    }
    .album-cover-grid{
        top: 120px;
    }
}
  
@media (max-width: 730px) {
    .album-cover-grid {
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: repeat(7, 1fr);
    }
}
 
@media (max-width: 550px) {
    .album-container{
        /* top: 180px; */
        width: 200px;
        height: 200px;
        
    }
    .album-cover-grid {
        /* top:180px; */
        grid-template-columns: repeat(1, 1fr);
        grid-template-rows: repeat(20, 1fr);
    /* grid-template-columns: repeat(auto-fit, minmax(200px, 1fr)); */
    }

    .album-cover {
        width: 198px;
        height: 198px;
    }

    .album-hover{
        -webkit-transform: translate(24%, -237%);
                transform: translate(24%, -237%);
    }
}

@media (max-width: 500px){
    .grid-container{
        top: 180px;
    }


}


