.navbar {
  position: fixed;
  width: 100%;
  height: var(--nav-size);
  background-color: var(--bg);
  -webkit-padding-start: 0 1rem;
          padding-inline-start: 0 1rem;
  border-bottom: var(--border);
  z-index: 500;
}

.navbar-nav{
  max-width: 100%;
  height: 100%;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  margin-left: 10px;
}

.nav-item{
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
